<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班级公告</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="$router.push({path:'/issueNotice'})">发布</span>
        </div>
        <div style="padding:0.84rem 0 1.7rem;">
            <div class="pl-30">
                <van-tabs v-model="activeTab" color="#6b77ff" class="classNote">
                    <van-tab title="班级公告">
                        <ul>
                            <van-list v-model="loading" :finished="finished" @load="onLoad" :immediate-check="false">
                                <!-- <van-cell  > -->
                                    <li class="pr-30 b_line" @click="lookDetail(item.id,1)"   v-for="(item,index) in list" :key="index">
                                        <div>
                                            <p class="f28 c333">{{item.title}}</p>
                                            <!-- <img :style="{display:item.isSee?'none':'inline-block'}" src="../../assets/message/notSee.png" alt=""> -->
                                        </div>
                                        <div class="mt-22">
                                            <p class="f26 c666" style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width:80%;">{{item.afficheDesc}}</p>
                                            <p class="f22 c999">{{item.createTime.substring(0,10)}}</p> 
                                        </div>
                                    </li>

                                <!-- </van-cell> -->
                            </van-list>
                            
                        </ul>
                    </van-tab>
                    <van-tab title="班级活动">
                        <ul>
                            <van-list v-model="loadingActive" :finished="finishedActive" @load="onLoadActive" :immediate-check="false">
                                <li class="pr-30 b_line" v-for="(item,index) in listActive" :key="index" @click="lookDetail(index)">
                                    <div>
                                        <p class="f28 c333">{{item.title}}</p>
                                        <!-- <img :style="{display:item.isSee?'none':'inline-block'}" src="../../assets/message/notSee.png" alt=""> -->
                                    </div>
                                    <div class="mt-22">
                                        <p class="f26 c666" style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width:80%;">{{item.afficheDesc}}</p>
                                        <p class="f22 c999">{{item.createTime.substring(0,10)}}</p> 
                                    </div>
                                </li>
                            </van-list>
                        </ul>
                    </van-tab>
                </van-tabs>
                
            </div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            activeTab:0,
            param : {
                classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                state:1,//班级公告
                page:1,
                size:10,
                total:0
            },
            paramActive : {
                classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                state:2,//班级活动
                page:1,
                size:10,
                total:0

            },
            loading: false,
            finished: false,
            loadingActive: false,
            finishedActive: false,
            list:[],//公告列表
            listActive:[],//活动列表
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        not(){
            this.active=true
            this.active1=false
        },
        yes(){
            this.active=false
            this.active1=true
        },
        lookDetail(id){
            if(this.activeTab == 0){
                this.$router.push({path:'/noticeDetail',query:{id:id,type:1}});//公告
            }else{
                this.$router.push({path:'/noticeDetail',query:{id:id,type:1}});//活动
            }
        },
        getAfficheClassList(){//公告列表
            this.$http({url:this.$api.message.getAfficheClassList},this.param).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        this.list.push(data.data[i]);
                    }
                    this.param.total = data.total;
                    this.param.page = ++this.param.page;
                }
            })
        },
        getAfficheClassListActive(){//活动列表
            this.$http({url:this.$api.message.getAfficheClassList},this.paramActive).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        this.listActive.push(data.data[i]);
                    }
                    this.paramActive.total = data.total;
                    this.paramActive.page = ++this.paramActive.page;
                }
            })
        },
        onLoad() {
            setTimeout(() => {
                if (this.param.total >= this.param.page) {
                    this.finished = true;
                    this.loading = false;
                    return
                }
                // this.loading = true;
                this.getAfficheClassList();
            }, 500);
        },
        onLoadActive() {
            setTimeout(() => {
                if (this.paramActive.total >= this.paramActive.page) {
                    this.finished = true;
                    this.loading = false;
                    return
                }
                // this.loading = true;
                this.getAfficheClassListActive();
            }, 500);
        }
    },
    created() {
        this.getAfficheClassList();
        this.getAfficheClassListActive();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    
    li{
        padding:0.3rem 0.3rem 0.2rem 0;
    }
    li div{
        display: flex;
        justify-content: space-between;
        align-items: center
    }
    li img{
        width:0.14rem;
        height:0.14rem;
    }
</style>




